.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #f8f8f8;
  height: 100px;
  padding: 20px;
  color: black;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.dogContainer {
  padding-top: 15px;
  margin: auto;
  width: 80%;
  max-width: 540px;
  height: 100%;
}

.dogMedia {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
}
